import React from 'react'
import { connect } from 'react-redux'
import { Redirect, Route, Switch } from 'react-router-dom'
import { Container } from '@material-ui/core'

import Categories from './Categories/Categories'
import Header from './Header'
import Ingredients from './Ingredients/Ingredients'
import ManageUsers from './Admin/ManageUsers'
import Meals from './Meals/Meals'
import Navigation from './Navigation'
import NotFound from './NotFound'
import PageWrapper from './PageWrapper'
import Plans from './Plans/Plans'
import ProtectedRoute from './ProtectedRoute'
import Recipes from './Recipes/Recipes'
import Settings from './Settings'
import { PATHS } from '../constants/paths'

const mapStateToProps = (state) => ({
  currentUser: state.common.currentUser,
})

const Home = ({ currentUser }) => {
  if (!currentUser) return <Redirect to='/login' />

  return (
    <>
      <Header currentUser={currentUser} />
      <PageWrapper>
        <Container>
          <Switch>
            <ProtectedRoute component={Plans} path={`${PATHS.root}/plans`} />
            <ProtectedRoute component={Meals} path={`${PATHS.root}/meals`} />
            <ProtectedRoute
              component={Recipes}
              path={`${PATHS.root}/recipes`}
            />

            <ProtectedRoute
              component={Categories}
              path={`${PATHS.root}/ingredients/categories`}
            />
            <ProtectedRoute
              component={Ingredients}
              path={`${PATHS.root}/ingredients`}
            />

            <ProtectedRoute
              component={Settings}
              path={`${PATHS.root}/settings`}
            />
            <ProtectedRoute
              component={ManageUsers}
              path={`${PATHS.root}/users`}
            />

            <Route component={NotFound} />
          </Switch>
        </Container>
        <Navigation />
      </PageWrapper>
    </>
  )
}

export default connect(mapStateToProps, null)(Home)
